<template>
  <div
    class="QAModal modal fade"
    id="QAModal"
    tabindex="-1"
    aria-labelledby="QAModalLabel"
    aria-hidden="true"
  >
    <div id="QAModal" class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header bg-success">
          <h5 class="modal-title" id="QAModalLabel">回覆</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="py-3">
            <p>
              {{QADetail.name}}
              <small class="text-muted">({{QADetail.question_date}})</small>
            </p>
            <p class="m-0">：{{QADetail.question}}</p>
          </div>
          <div v-if="QADetail.answer != ''" class="bg-light border p-3">
            <p>你的回覆：{{QADetail.answer}}</p>
            <p class="m-0 text-right text-muted">
              <small>({{QADetail.answer_date}})</small>
            </p>
          </div>
          <div v-if="QADetail.answer == ''" class="form-group mt-3">
            <hr />
            <ValidationObserver v-slot="{ invalid }">
              <form @submit.prevent="sendReply">
                <ValidationProvider rules="required" v-slot="{ errors,classes }">
                  <label class="h5" for="reply">回覆</label>
                  <span class="text-danger">{{errors[0]}}</span>
                  <textarea
                    class="form-control"
                    id="reply"
                    rows="3"
                    :class="classes"
                    v-model.trim="answerBox"
                  ></textarea>
                </ValidationProvider>
                <button
                  type="submit"
                  class="btn btn-success mt-2 w-100"
                  :disabled="invalid"
                  :class="{disabled:invalid}"
                >送出</button>
              </form>
            </ValidationObserver>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-success" data-dismiss="modal">關閉</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// /* global $ */
import Swal from 'sweetalert2/dist/sweetalert2.js'
import 'sweetalert2/src/sweetalert2.scss'
export default {
  data () {
    return {
      answerBox: ''
    }
  },
  name: 'QAModal',
  props: ['QADetail'],
  methods: {
    sendReply: function () {
      const config = {
        method: 'post',
        url: 'http://pettrip.rocket-coding.com/api/Qa/PostAnswer',
        data: {
          queseq: this.QADetail.queseq,
          message: this.answerBox
        }
      }

      this.$http(config)
        .then(function (response) {
          console.log(response)
          Swal.fire({
            toast: true,
            position: 'top-end',
            icon: 'success',
            title: '回覆成功',
            showConfirmButton: false,
            timer: 2000
          })
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>
